import {TextField, Typography, makeStyles, FormControl, Select, MenuItem, Button } from "@material-ui/core";
import { useState } from "react";

const useStyles = makeStyles({
    otsikko : {
        marginTop : 10,
        marginBottom : 20,
        fontSize : 18
    },
    syote : {
        marginBottom : 10
    },
    tulos : {
        marginTop : 10
    }
});

function Laskuri(props) {

    const [muunnettavaSumma, setMuunnettavaSumma] = useState();
    const [kohdeValuutta, setKohdeValuutta] = useState("USD");
    const [tulosteksti, setTulosteksti] = useState("");

    const laskeMuunnos =  () => { 
        

        if (isNaN(muunnettavaSumma)) {
            setTulosteksti("Syötä ainoastaan numeroita");
        }
        else{
            let tulos = muunnettavaSumma * props.valuutat[kohdeValuutta];
            setTulosteksti(`${muunnettavaSumma} EUR = ${tulos} ${kohdeValuutta}`);
        }


    }

    const tyylit = useStyles();

    return(
        <>

        <TextField 
        className={tyylit.syote} 
        variant="outlined" 
        label="Anna muunnettava summa (euro)" 
        fullWidth
        onChange={(e) => {setMuunnettavaSumma(e.target.value)}}
        />

        <FormControl 
        className={tyylit.syote} 
        variant="outlined" fullWidth
        >
            <Select value={kohdeValuutta} displayEmpty onChange={(e) => {setKohdeValuutta(e.target.value)}}>

                {Object.keys(props.valuutat).sort().map((valuutta) => {
                    return <MenuItem value={valuutta}>{valuutta} ({props.valuutat[valuutta]})</MenuItem>
                })}

            </Select>
        </FormControl>

        <Button variant="contained" color="primary" fullWidth size="Large" onClick={laskeMuunnos}>
            Laske muunnos
        </Button>

        <Typography className={tyylit.tulos}>{tulosteksti}</Typography>

        </>
    );
}

export default Laskuri;